export enum VisibilityTarget {
    ScrollyOnly = 'scrolly-only',
    DashboardOnly = 'dashboard-only',
    Onboarding = 'onboarding'
}

export class VisibilityManager {
    // Used to keep initial css display value
    private nodes: Map<HTMLElement, string> = new Map();

    hide(target: VisibilityTarget): void {
        document.querySelectorAll<HTMLElement>(`.${target}`).forEach(elem => {
            if (!this.nodes.has(elem)) {
                this.nodes.set(elem, elem.style.display);
            }
            elem.removeAttribute('entering')
            elem.style.display = 'none';
        })
    }

    show(target: VisibilityTarget): void {
        document.querySelectorAll<HTMLElement>(`.${target}`).forEach(elem => {
            const display = this.nodes.get(elem) ?? 'block'
            elem.setAttribute('entering', '')
            elem.style.display = display;
        })
    }
}
