import * as viz from './scripts/viz'
import * as utils from './scripts/utils'
import * as types from './scripts/types'
import qualifs from "./assets/data/parsed_quali.json";
import races from "./assets/data/parsed_races.json";
import * as d3 from 'd3'
import { ScrollManager, ScrollyEvent } from './scripts/scroll-event';
import { VisibilityManager, VisibilityTarget } from './scripts/visibility-manager';
import { OnboardingManager } from './scripts/onboarding-manager';
import { ContentManager } from './scripts/content-manager';

/**
 * Main
 */
(function (d3) {
    const onboardingManager = new OnboardingManager()
    const scrollManager = new ScrollManager(d3, onboardingManager)
    const visibilityManager = new VisibilityManager()
    const contentManager = new ContentManager();
    visibilityManager.hide(VisibilityTarget.DashboardOnly)
    const onboard = document.querySelector<HTMLElement>(`#onboarding`)
    onboard.classList.add('visible')
    scrollManager.onScrollyStateChange(state => {
        if (state == ScrollyEvent.Finished) {
            visibilityManager.show(VisibilityTarget.DashboardOnly)
            visibilityManager.hide(VisibilityTarget.ScrollyOnly)
            d3.select<HTMLInputElement, unknown>('#constructor-option-max')
                .node()
                .checked = true
            d3.select<HTMLInputElement, unknown>('#constructor-option-min')
                .node()
                .checked = true
            d3.select<HTMLDivElement, unknown>('#select-constructor').node().dispatchEvent(new Event('change'))
        } else {
            visibilityManager.hide(VisibilityTarget.DashboardOnly)
            visibilityManager.show(VisibilityTarget.ScrollyOnly)
        }
    })

    let scrollyOpt: utils.SCROLLYOPTION = null
    let constructorsToPlot = []
    const minMaxYears = [1994, 2021]
    const length = minMaxYears[1] - minMaxYears[0]

    scrollManager.onScrollyProgress(({ progress, forward }) => {
        const yearProgress = Math.round(minMaxYears[0] + ((progress / 100) * length))
        const yearsToPlot = [minMaxYears[0], yearProgress]
        if (contentManager.periodChanged(progress)) {
            const content = contentManager.getContent();
            scrollyOpt = content.tag;
            constructorsToPlot = content.constructors
            d3.select('#scrolly-content').html(content.html)
            if (forward) {
                scrollManager.reachedCheckpoint()
            }
        }
        for (let i = 1; i < 5; i++) {
            updateGraph(i, constructorsToPlot, scrollyOpt, !scrollManager.scrollyFinished, yearsToPlot)
        }

    })

    const baseGPs = [utils.GP.AUSTRALIAN, utils.GP.BELGIAN, utils.GP.MONACO, utils.GP.BRAZILIAN]
    const baseConstructors = []

    const selectConstructor = d3.select('#select-constructor')
    selectConstructor.on('change', () => {
        const constructors = getSelectedConstructors()
        for (let i = 1; i < 5; i++)
            updateGraph(i, constructors, null, !scrollManager.scrollyFinished)
    })
    utils.addSelectOptionsConstructors(selectConstructor.node(), baseConstructors)

    for (let i = 1; i < 5; i++) {
        const selectGP = d3.select<HTMLSelectElement, unknown>('#select-gp-' + i)
        selectGP.on('change', () => updateGraph(i, getSelectedConstructors(), null, !scrollManager.scrollyFinished))
        d3.select('#select-gp-type-' + i)
            .on('change', () => updateGraph(i, getSelectedConstructors(), null, !scrollManager.scrollyFinished))
        utils.addSelectOptionsGps(selectGP.node(), baseGPs[i - 1])
        // viz.makeLineChart(qualifs, baseGPs[i - 1], "#graph-" + i, width, height, margin, baseConstructors, null, !scrollManager.scrollyFinished)
        updateGraph(i, getSelectedConstructors(), null, true, [1994, 1994])
    }
})(d3)

function getSelectedConstructors() {
    const selectConstructor = d3.select<HTMLDivElement, unknown>('#select-constructor').node()
    const displayedConstructors = []
    const children = selectConstructor.children
    for (let i = 0; i < children.length; i++) {
        const child = children[i].children[0];
        if (utils.isInputElem(child) && child.checked) {
            displayedConstructors.push(child.value);
        }
    }
    return displayedConstructors
}

function updateGraph(graphId: number,
    constructors: utils.CONSTRUCTOR[],
    scrollyOpt: utils.SCROLLYOPTION = null,
    scrollyMode: boolean,
    yearRange?: number[]) {
    const margin: types.Margins = { top: 30, right: 30, bottom: 30, left: 60 }
    const svgWidth: number = (innerWidth / 2) - 200
    const svgHeight: number = (innerHeight / 2) - 110

    const width: number = svgWidth - margin.left - margin.right
    const height: number = svgHeight - margin.top - margin.bottom

    const gpName = d3.select('#select-gp-' + graphId).property('value')
    const gpType = d3.select('#select-gp-type-' + graphId).property('value')

    let data = gpType === 'race' ? races : qualifs
    let filteredData: Object = {}

    if (yearRange) {
        for (const [year, results] of Object.entries(data)) {
            if (yearRange[0] <= +year && +year < yearRange[1]) {
                filteredData[year] = results
            } else {
                filteredData[year] = null
            }
        }
    } else {
        if (Object.keys(filteredData).length == 0) // Redundant check, since it should be empty if we don't enter previous if
            filteredData = data
    }
    
    if (gpType === 'race') {
        viz.makeLineChart(filteredData, gpName, "#graph-" + graphId, width, height, margin, constructors, scrollyOpt, scrollyMode)
    } else {
        viz.makeLineChart(filteredData, gpName, "#graph-" + graphId, width, height, margin, constructors, scrollyOpt, scrollyMode)
    }
}
