
const HINTS = [
    {
        msg: "Cette piste de course permet de visualiser la progression du Scrolly-Telling. Les cercles rouges reprsentent des changements d'poque importante.",
        target: '.progress-svg',
        offset: 230,
    },
    {
        msg: "Sur les graphiques, les lignes rouges verticales reprsentent un changement dans le rglement de la Formule 1. Vous allez pouvoir les survolez  la fin du scrolly pour avoir plus de dtails.",
        target: '#graph-2',
        offset: 220,
    },
    {
        msg: "Une fois arriv  la fin, diffrents contrles apparatront et vous serez libre de slectionner par vous mme un grand prix spcifique, et comparer les temps de course avec les temps en qualification. \n\nBonne dcouverte!",
        target: '.progress-svg',
        offset: 450,
    },
];

const ID = 'onboarding-popup'
export class OnboardingManager {
    private wrapper: HTMLDivElement;
    private textElem: HTMLParagraphElement;
    private cbs = [];
    private currentHint = -1;
    private completed = false;

    constructor() {
        this.wrapper = document.createElement('div');
        this.wrapper.setAttribute('id', ID);
        this.textElem = document.createElement('p');
        const button = document.createElement('button');
        button.innerText = "Continuer";
        button.addEventListener('click', () => {
            this.removeHint();
            this.nextMessage();
        });

        this.wrapper.append(this.textElem);
        this.wrapper.append(button);

    }

    startOnboarding(): void {
        if (this.completed) return;

        this.nextMessage();
    }


    onOnboardingFinished(cb: () => void): void {
        this.cbs.push(cb)
    }

    private nextMessage(): void {
        if (this.currentHint + 1 == HINTS.length) {
            this.completed = true;
            this.cbs.forEach(cb => cb());
            return;
        }
        this.currentHint += 1;
        const hint = HINTS[this.currentHint];
        const target = document.querySelector<HTMLElement>(hint.target);
        const boundingRect = target.getBoundingClientRect();
        const hintLeft = boundingRect.left + boundingRect.width / 2 - 100;
        const hintTop = boundingRect.top - hint.offset;
        this.render(hintLeft, hintTop, hint.msg);
    }

    private render(x: number, y: number, message: string): void {
        this.textElem.innerText = message;
        this.wrapper.style.left = `${x}px`;
        this.wrapper.style.top = `${y}px`;
        document.body.append(this.wrapper);
    }

    private removeHint(): void {
        document.body.removeChild(this.wrapper);
    }



}
