import * as preprocess from './preprocess'
import * as utils from './utils'
import * as types from './types'
import * as d3 from 'd3'
import d3Tip from 'd3-tip'

/**
 * Build a d3 X Scale from the x values to draw the linechart
 * @param X array of dates
 * @param width witdh of the plot
 * @returns X scale for the linechart
 */
export function getXScale(X: Date[], width: number): any {
    const xDomain = d3.extent(X)
    return d3.scaleTime()
        .domain(xDomain)
        .range([0, width])
}

/**
 * Build a d3 Y Scale from the y values to draw the linechart
 * @param Y array laptimes (can contain null)
 * @param height witdh of the plot
 * @returns Y scale for the linechart
 */
function getYScale(Y: number[], height: number): any {
    return d3.scaleLinear()
        .domain(utils.Y_MINMAX)
        .range([height, 0])
}

/**
 * Build an svg to draw a linechart in
 * @param svgId id of the created svg
 * @param width svg width
 * @param height svg height
 * @param margin svg margins
 * @returns built svg
 */
function buildSvg(svgId: string, width: number, height: number, margin: types.Margins) {
    d3.select(svgId).selectAll("*").remove();
    return d3.select<SVGElement, any>(svgId)
        .append("svg")
        .attr('width', '90%')
        .attr('height', '90%')
        .attr("viewBox", '0 0 '+(width + margin.left + margin.right)+ ' ' + (height + margin.bottom + margin.top))
        .attr('id', (svgId + '-svg').replace('#', ''))
        .append("g")
        .attr('class', 'main-g')
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")")
}

/**
 * Get the driver and constructor name for a hovered point
 * @param d hovered point data
 * @param keyword min for mintime, max for maxtime, otherwise it should be the constructor's name
 * @returns tooltip content
 */
function getHoverContent(d: types.Race, keyword: string): string {
    if (!d.results) {
        return ''
    }
    let constructor = ''
    let pilot = ''
    let time = ''
    if (keyword === 'max') {
        const maxIndex = d3.maxIndex(d.results, (yearResults: any) => yearResults.fastest)
        constructor = 'Worst time - ' + d.results[maxIndex].constructor
        pilot = d.results[maxIndex].driver
        time = utils.getHumanReadableTime(d.results[maxIndex].fastest * 1000)
    } else if (keyword === 'min') {
        const minIndex = d3.minIndex(d.results, (yearResults: any) => yearResults.fastest)
        constructor = 'Best time - ' + d.results[minIndex].constructor
        pilot = d.results[minIndex].driver
        time = utils.getHumanReadableTime(d.results[minIndex].fastest * 1000)
    } else {
        const point = d.results.find((result) => result["constructor"].toLowerCase() === keyword.toLowerCase())
        constructor = keyword
        pilot = point.driver
        time = utils.getHumanReadableTime(point.fastest * 1000)
    }
    return `<p style="font-weight: bold">${constructor}</p><p>Driver: <i>${pilot}</i></p><p>Time: <i>${time}</i></p>`
}

/**
 * Make all unhovered lines and points have an opacity of 0.5
 * @param node hovered node that called this function. We don't touch the opacity of it.
 * @returns
 */
function addLineAndDotTransparency(node: SVGElement): void {
    const hoveredClass = node.parentElement.getAttribute('class')
    for (let i = 1; i < 5; i++) {
        d3.select('#graph-' + i + '-svg')
            .select('g.main-g')
            .selectChildren()
            .nodes()
            .filter((element: SVGElement) =>
                ![...utils.FIXED_CLASSES_OPACITY, hoveredClass].includes(element.getAttribute('class')))
            .forEach((el: SVGElement) => el.setAttribute('style', 'opacity: 0.3'))
    }
}

/**
 * Make all lines and dots opaque
 * @returns void
 */
function removeLineAndDotTransparency(): void {
    for (let i = 1; i < 5; i++) {
        d3.select('#graph-' + i + '-svg')
            .select('g.main-g')
            .selectChildren()
            .nodes()
            .filter((element: SVGElement) =>
                !utils.FIXED_CLASSES_OPACITY.includes(element.getAttribute('class')))
            .forEach((el: SVGElement) => el.setAttribute('style', 'opacity: 1'))
    }
}

/**
 * Build an SVG containing a graph
 * @param rawData raw data
 * @param gpName Name of the grand prix in data, usually '<country> Grand Prix'
 * @param divId id of the div that will contain the chart
 * @param width width of the chart
 * @param height height of the chart
 * @param margin margins
 * @param constructors list of constructors to plot
 * @param scrollyMode boolean, true if the user is still in the scrolly, false otherwise
 * @param scrollyOpt SCROLLYOPTION : an enum that defines the scrolly step we are in
 */
export function makeLineChart(rawData,
    gpName: string,
    divId: string,
    width: number,
    height: number,
    margin: types.Margins,
    constructors: utils.CONSTRUCTOR[],
    scrollyOpt: utils.SCROLLYOPTION = null,
    scrollyMode: boolean): void {
    const data = preprocess.extractGP(rawData, gpName)
    let svg = buildSvg(divId, width, height, margin)
    draw(data, svg, width, height, gpName, constructors, scrollyOpt, scrollyMode)
    drawLegend(constructors, scrollyMode)
}

/**
 * Draws the actual graph. Will draw all the available constructors in the constructors argument.
 * @param data data to draw
 * @param svg svg to draw in
 * @param width svg width
 * @param height svg height
 * @param mainTitle line chart title
 * @param constructors list of constructors to plot
 * @param scrollyMode boolean, true if the user is still in the scrolly, false otherwise
 * @param scrollyOpt SCROLLYOPTION : an enum that defines the scrolly step we are in
 */
function draw(data,
    svg,
    width: number,
    height: number,
    mainTitle: string,
    constructors: string[],
    scrollyOpt: utils.SCROLLYOPTION = null,
    scrollyMode: boolean): void {
    const X = preprocess.extractDates(data)
    console.log(mainTitle,width,height)
    const regulationDates: Date[] = preprocess.extractRegulationDates()

    const YMin = preprocess.extractBestTimes(data)
    const YMax = preprocess.extractWorstTimes(data)

    let toPlot = []

    if (scrollyMode) {
        toPlot = [
            { data: YMin, name: 'min', color: 'purple' },
            { data: YMax, name: 'max', color: 'black' }
        ]
    } else {
        if (constructors.includes('min')) {
            toPlot.push({ data: YMin, name: 'min', color: 'purple' })
        }
        if (constructors.includes('max')) {
            toPlot.push({ data: YMax, name: 'max', color: 'black' })
        }
    }

    for (const constructor of constructors) {
        toPlot.push({
            data: preprocess.extractConstructor(data, constructor),
            name: constructor,
            color: utils.getConstructorColor(constructor)
        })
    }

    /* define the scales */
    const allY = []
    for (const Y of toPlot) {
        allY.push(...Y.data)
    }
    const xScale = getXScale(X as any, width)
    const yScale = getYScale(allY, height)

    /* bind the data */
    svg.datum(data)

    /* draw x and y axis + their labels */
    svg.append("g")
        .attr("class", "xaxis")
        .attr("transform", "translate(0," + height + ")")
        .call(d3.axisBottom(xScale))
        .call(g => g.append("text")
            .attr("x", width + 5)
            .attr("y", 5)
            .attr("fill", "currentColor")
            .attr("text-anchor", "start")
            .text('Year'));
    svg.append("g")
        .attr("class", "yaxis")
        .call(d3.axisLeft(yScale))
        .call(g => g.append("text")
            .attr("x", -25)
            .attr("y", -10)
            .attr("fill", "currentColor")
            .attr("text-anchor", "start")
            .text('Lap Time (s)'));

    /* draw the regulations red bars */
    const regulationPaths = svg.append('g')
        .attr('class', 'regulations')
        .selectAll('path')
        .data(regulationDates.map((d) => { return [{ x: d, y: utils.Y_MINMAX[0] }, { x: d, y: utils.Y_MINMAX[1] }] }))
        .enter()
        .append('path')
        .attr("class", "regulationline")
        .attr("fill", "none")
        .attr("stroke-width", 2)
        .attr("opacity", 0.3)
        .attr("stroke", "red")
        .attr("d", (d) => {
            return d3.line()
                .x((d: any) => xScale(d.x))
                .y((d: any) => yScale(d.y))(d)
        })

    if (!scrollyMode) {
        /* add the regulations red bars events if we are in the dashboard mode */
        regulationPaths.on("click", function (_, d) {
            const year = (d[0].x.getFullYear() + 1).toString()
            const index = preprocess.getIndexOfYear(year)
            const regDiv: d3.Selection<HTMLElement, any, HTMLElement, any> = d3.select('#regulation-div')
            if (regDiv.attr('class') === 'regulation-visible') {
                const isThereAFocusedEvent = d3.select('#graph-' + 1)
                    .select('g .regulations')
                    .selectAll('path')
                    .filter((_, i) => i === index)
                    .attr('opacity') === '0.7'

                for (let i = 1; i < 5; i++) {
                    d3.select('#graph-' + i)
                        .select('g .regulations')
                        .selectAll('path')
                        .attr('opacity', 0.3)
                }
                if (isThereAFocusedEvent) {
                    return
                }
            }
            for (let i = 1; i < 5; i++) {
                d3.select('#graph-' + i)
                    .select('g .regulations')
                    .selectAll('path')
                    .attr('opacity', 0.3)
            }
            regDiv.attr('class', 'regulation-visible')
            for (let i = 1; i < 5; i++) {
                d3.select('#graph-' + i)
                    .select('g .regulations')
                    .selectAll('path')
                    .filter((_, i) => i === index)
                    .attr('opacity', 0.7)
            }
        })
            .on("mouseover", function (_, d) {
                const year = (d[0].x.getFullYear() + 1).toString()
                const regDiv: d3.Selection<HTMLElement, any, HTMLElement, any> = d3.select('#regulation-div')
                const description = preprocess.extractRegulation(year)[1][0].description
                if (regDiv.attr('class') === 'regulation-invisible') {
                    regDiv.attr('class', 'regulation-visible')
                    regDiv.node().innerHTML = `<p>${description}</p>`
                }
                d3.select(this).style("cursor", "pointer")
                    .attr('stroke-width', 4)
            })
            .on("mouseout", function (_, d) {
                const year = (d[0].x.getFullYear() + 1).toString()
                const index = preprocess.getIndexOfYear(year)
                const isPathFocused = d3.select('#graph-' + 1)
                    .select('g .regulations')
                    .selectAll('path')
                    .filter((_, i) => i === index)
                    .attr('opacity') === '0.7'
                if (!isPathFocused) {
                    d3.select('#regulation-div').attr('class', 'regulation-invisible')
                }
                d3.select(this).style("cursor", "default")
                    .attr('stroke-width', 2)
            })
    } else {
        /* draw the scrolly step we are in if there is one, and add the title */
        switch (scrollyOpt) {
            case utils.SCROLLYOPTION.ANNEES_2000: {
                drawRectangle(2000, 2010, xScale, yScale, svg)
                break
            }
            case utils.SCROLLYOPTION.FERRARI_VS_WILLIAMS: {
                drawRectangle(1994, 2000, xScale, yScale, svg)
                break
            }
            case utils.SCROLLYOPTION.ANNEES_2010: {
                drawRectangle(2009, 2014, xScale, yScale, svg)
                break
            }
            case utils.SCROLLYOPTION.TURBOHYBRID: {
                drawRectangle(2014, 2021, xScale, yScale, svg)
                break
            }
            default:
                break
        }
        svg.append('g')
            .attr('class', 'main-title')
            .append('text')
            .attr('x', width / 2)
            .attr('y', -10)
            .attr('text-anchor', 'middle')
            .text(mainTitle)
    }

    /* remove all existing d3 tips for this graph */
    const svgId = svg.node().parentElement.getAttribute('id')
    d3.selectAll('div.tip-' + svgId)
        .nodes()
        .forEach((node: HTMLElement) => node.remove())
    
    /* draw all the possible constructors */
    for (let idx = 0; idx < toPlot.length; idx++) {
        const Y = toPlot[idx]
        const Ydata = Y.data
        const isPlottingMinOrMax = scrollyMode ? false : ["min", "max"].includes(Y.name)

        const svgToPlot = svg.append('g')
            .attr('class', Y.name.replace(' ', ''))

        /* configure the line object and the tooltip */
        let line = d3.line()
            .defined(function (_, i) { return !!Ydata[i]; })
            .x(function (_, i) { return xScale(X[i]); })
            .y(function (_, i) { return yScale(Ydata[i]); })

        const tipCircles = d3Tip()
            .attr('class', 'd3-tip tip-' + svgId)
            .html(function (d) {
                return getHoverContent(d, Y.name)
            })
        svg.call(tipCircles)

        if (!isPlottingMinOrMax) {
            svgToPlot.append("path")
                .attr("class", "line")
                .attr("fill", "none")
                .attr("stroke-width", 2)
                .attr("stroke", Y.color)
                .attr("d", i => {
                    return line(i)
                })
                .on('mouseover', function () { addLineAndDotTransparency(this as SVGElement) })
                .on('mouseout', function () { removeLineAndDotTransparency() })
        }

        /* draw the circles and add the hover events */
        svgToPlot.selectAll(".dot")
            .data(data)
            .enter()
            .append("circle")
            .attr("class", "dot")
            .attr("cx", line.x())
            .attr("cy", line.y())
            .attr('fill', isPlottingMinOrMax ? 'white' : Y.color)
            .attr('stroke', Y.color)
            .attr('stroke-width', 1)
            .attr("r", (_, i) => {
                if (Ydata[i]) {
                    return isPlottingMinOrMax ? 4 : 3
                }
                return 0
            })
            .on('mouseover', function (_, d) {
                tipCircles.show(d, this)
                addLineAndDotTransparency(this as SVGElement)
            })
            .on('mouseout', function (_, d) {
                tipCircles.hide(d, this)
                removeLineAndDotTransparency()
            })
    }
}

/**
 * Draws the legend. The legends is updated every time the user selects/deselects a constructor
 * @param constructors list of constructors to plot
 * @param scrollyMode boolean, true if the user is still in the scrolly, false otherwise
 */
export function drawLegend(constructors: utils.CONSTRUCTOR[], scrollyMode: boolean): void {
    const data = scrollyMode ? ['min', 'max'] : []
    data.push(...constructors)
    const legend = d3.select('#legend')
    legend.selectAll('g.legendItem').remove()
    const group = legend.selectAll('g.legendItem')
        .data(data)
        .enter()
        .append('g')
        .attr('class', 'legendItem')

    group.append("circle")
        .attr("cx", (_, i) => i * 130 + 10)
        .attr("cy", 10)
        .attr("r", 6)
        .style("fill", (d) => utils.getConstructorColor(d as any))

    group.append("text")
        .attr("x", (_, i) => i * 130 + 20)
        .attr("y", 14)
        .text(d => {
            if (d === 'min') {
                return 'Meilleur temps'
            } else if (d === 'max') {
                return 'Pire temps'
            }
            return d
        })
        .style("font-size", "15px")
        .attr("alignment-baseline", "middle")
}

/**
 * Draws a green rectangle to enhance a particular period of the svg node.
 * @param yearStart lower left bound of the rectangle
 * @param yearStop lower right bound of the rectangle
 * @param xScale xScale
 * @param yScale yScale
 * @param svg svg node
 */
function drawRectangle(yearStart: number, yearStop: number, xScale: d3.ScaleTime<number, number>, yScale: d3.ScaleLinear<number, number>, svg: any) {
    const rect = {
        x: xScale(new Date(yearStart + '-01-01')),
        y: 0,
        width: xScale(new Date(yearStop + '-01-01')) - xScale(new Date(yearStart + '-01-01')),
        height: yScale(utils.Y_MINMAX[0]),
    }
    svg.append('rect')
        .attr('class', 'scrolly-rect')
        .attr('x', rect.x)
        .attr('y', rect.y)
        .attr('height', rect.height)
        .attr('width', rect.width)
        .attr('fill', 'green')
        .attr('opacity', 0.1)
}
